/* ----------Font Family---------- */
@import url('https://fonts.googleapis.com/css2?family=Cinzel+Decorative:wght@400;700;900&family=Exo+2:ital,wght@0,100..900;1,100..900&family=Satisfy&display=swap');





/* ----------Common Styling start---------- */
section {
    min-height: 100vh;
}


:root {
    --primary-color: #E1EBFA;
    --secondary-color: #dff9fb;
    --third-color: #f7f1e3;


    --header-color: #e1ebfa2f;
    --main-color: #A20100;
    --text-color: #120f0e;
    --secondary-text-color: #848485;
    --white-color: white;
    --btn-color: #0048b5;

    --footer-color: #1a1a21;
    --footer-copyright-color: #000000;

    --primary-font: 'exo 2';
    --secondary-font: 'Satisfy';
    --brand-font: "Cinzel Decorative";
}



.brand {
    font-family: var(--brand-font);
    font-weight: 800;
    color: var(--btn-color);
}

.brand span {
    font-family: var(--brand-font);
    background: none;
    color: var(--main-color);
}

.content-center {
    display: flex;
    justify-content: center;
    align-items: center;
}

body {
    color: var(--text-color);
    font-family: var(--primary-font);
}

.btn {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 1.5rem 1.5rem 1.5rem 0;
    padding: .8rem 1.5rem;
    border: 1px solid;
    font-weight: 700;
    border: .2rem solid transparent;
    transition: .5s;
}

.btn:hover {
    border-color: var(--btn-color);
    color: var(--btn-color);
}

.btn i {
    font-size: 1.5rem;
    transition: .5s ease;
}

.btn:hover i {
    padding-left: .7rem;
}

.btn-main {
    color: white;
    background-color: var(--btn-color);
    transition: .5s;
    box-shadow: var(--primary-color);
}

.btn-main:hover {
    backdrop-filter: blur(.3rem);
    border-color: var(--btn-color);
    color: var(--btn-color);
}

span {
    z-index: 9;
    color: var(--main-color);
    font-family: var(--secondary-font);


    background-image: linear-gradient(#e1ebfa10, #f3d48a9d);
}

.btn-secondary-main {
    color: white;
    background-color: var(--main-color);
    transition: .5s;
    box-shadow: var(--primary-color);
}

.btn-secondary-main:hover {
    backdrop-filter: blur(.3rem);
    border-color: var(--main-color);
    color: var(--main-color);
}

.page-heading {
    padding: 4rem 0;
    text-align: center;
    font-weight: 700;
    font-size: 4rem;
    line-height: 4.5rem;
}

.page-heading span {
    font-family: var(--secondary-font);
    color: var(--main-color);
}

.card-container {
    display: grid;
    grid-template-columns: auto auto auto auto;
    gap: 2.5rem;
}

.card {
    background-color: var(--white-color);
    border: 0;
    flex: 1;
}

.card .icon {
    padding: 1rem;
}

.card .icon i {
    border-radius: 1rem;
    background-color: var(--primary-color);
    color: var(--main-color);
    font-size: 3.5rem;
    padding: .3rem;
}


.card .card-header {
    padding-top: .5rem !important;
    margin: 0;
    font-size: 1.2rem;
    border: 0;
    font-weight: 700;
    background: transparent;
    padding: 0 1rem;
}

.list-group .list-group-item {
    display: flex;
    align-items: center;
    background: transparent;
}

.list-group .list-group-item i {
    font-size: 1.4rem;
    margin-right: 1rem;
    color: var(--text-color);

}

.card .card-body {
    padding-left: 1rem !important;
    padding: .3rem;
}

.card .card-body a.btn {
    margin-top: 1rem;
}

/* ----------Common Styling end---------- */






/* ----------Header start---------- */
header.navbar {
    padding: 1rem 2rem;
    z-index: 99;
    width: 100%;
    position: fixed;
    /* position: -webkit-sticky; */
    left: 0;
    top: 0;
    background-color: var(--header-color);
    backdrop-filter: blur(.3rem);
    transition: .7s ease;
}


header .navbar-nav .nav-item {
    margin: 0 .7rem;
}

header .navbar-nav .nav-item .nav-link {
    font-weight: 700;
}

header .navbar-nav .nav-item .nav-link:active {
    color: var(--main-color);
}

/* ----------Header end---------- */








/* ----------Home start---------- */
#home {
    background-color: var(--primary-color);
}

#home .home-bg {
    height: 85vh;
    background-image: url('https://png.pngtree.com/png-clipart/20231016/original/pngtree-businessman-pointing-at-camera-3d-render-businessman-character-illustration-png-image_13321561.png');

    background-size: cover;
    background-position: top;
}

#home .home-content {}

#home .home-content h4 {
    font-size: 4rem;
    font-weight: 600;
    text-transform: capitalize;
}

#home .home-content h4 span {
    padding: .5rem;
    font-size: 3rem;
    color: var(--main-color);
    font-family: var(--secondary-font);
    /* background-image: linear-gradient(#E1EBFA, skyblue); */
}

#home .home-content p {
    margin-top: 1rem;
    text-transform: capitalize;
    font-size: 1.5rem;
}

/* ----------Home end---------- */







/* ----------Services start---------- */
#services {
    padding-bottom: 3.5rem;
    background-color: var(--secondary-color);
}


/* ----------Services end---------- */











/* ----------Portfolio start---------- */
#portfolio {}

#portfolio .image-gallary {
    display: grid;
    grid-template-columns: auto auto auto;

    justify-content: center;
    align-items: center;
    gap: 1.5rem;
    padding: 1rem;
    padding-bottom: 3rem;
}

#portfolio .image-gallary .img {
    z-index: 1;
    border-radius: .4rem;
    height: 20rem;
    position: relative;
    overflow: hidden;
}

#portfolio .image-gallary .overlay {
    top: 0;
    left: 0;
    z-index: 99;
    position: absolute;
    height: 100%;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color: var(--white-color);
    visibility: hidden;
    transition: .3s ease;
}

#portfolio .image-gallary .overlay h5 {
    font-size: 2rem;
    font-weight: 700;
}

#portfolio .image-gallary .img:hover .overlay {
    visibility: visible;
}

#portfolio .image-gallary img {
    height: 100%;
    width: 100%;
    transition: .7s ease;
}

#portfolio .image-gallary .img:hover img {
    transform: scale(115%);
}

/* ----------Portfolio end---------- */










/* ----------About start---------- */
#about {
    background-color: var(--secondary-color);
}

#about .about-bg {
    background-image: url("https://bootstrapbrain.com/demo/templates/wave/wave-lite/assets/img/about/about-img-1.png");
    background-position: center;
    background-repeat: no-repeat;
}

#about h4 {
    padding: 3rem 0;
    font-size: 4.5rem;
    font-weight: 700;
}

#about p {
    font-size: 1.4rem;
}

#about .accordion .accordion-item .accordion-header button.accordion-button {
    color: var(--white-color);
    background-color: var(--btn-color);
}

#about .card {
    padding: 2.5rem;
    text-align: center;
}

#about .card .icon i {}

#about .card .card-header {
    font-size: 2.5rem;
}

#about .card .card-body {
    font-size: 1.2rem;
}

/* ----------About end---------- */














/* ----------Teams start---------- */
#team {
    padding-bottom: 4rem;
}

#team .card {
    overflow: hidden;
}

#team .card .img {
    height: 20rem;
    overflow: hidden;
}

#team .card .img img.card-img-top {
    height: 100%;
    transition: .5s ease;
}

#team .card img.card-img-top:hover {
    transform: scale(115%);
}

#team .card {
    padding: .5rem;
}


/* ----------Teams end---------- */














/* ----------Pricing start---------- */
#pricing {
    background-color: var(--third-color);
}

#pricing .content h4 {
    font-size: 5rem;
    font-weight: 600;
}

#pricing .content p {
    font-size: 1.8rem;
}



#pricing .card {
    padding: 1.3rem;
}

#pricing .card .card-header {
    line-height: 4rem;
}

#pricing .card .card-header h4 {
    font-size: 1.5rem;
    font-weight: 700;
}

#pricing .card .card-header .amount {
    font-size: 4rem;
    color: var(--btn-color);
}

#pricing .card .card-header p {
    font-size: 1rem;
    font-weight: 400;
}

/* ----------Pricing end---------- */













/* ----------Blog start---------- */
#blog {
    background-color: var(--secondary-color);
}

#blog .blog-content h4 {
    font-size: 4rem;
    font-weight: 700;
}

#blog .blog-content p {
    margin: 2rem 0;
    font-size: 1.5rem;
    margin-bottom: 3rem;
}

#blog .blog-post-container {
    display: grid;
    grid-template-columns: auto auto;
    gap: 1.5rem;
    padding: 3rem 0;
}

#blog .card {
    background: transparent;
}

#blog .card .card-img {
    border-radius: .3rem;
    position: relative;
    overflow: hidden;
    width: 100%;
    height: 16rem;
}

#blog .card .card-img img.card-img-top {
    width: 100%;
    height: 100%;
    transition: .7s ease;
}

#blog .card .card-img:hover img.card-img-top {
    transform: scale(117%);
}

#blog .card a {
    border: 0;
    color: white;
    font-size: 1.7rem;
    transition: .2s ease;
}

#blog .card .card-img .overlay {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;

    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    background-color: rgba(0, 0, 0, 0.511);
    color: var(--white-color);
    visibility: hidden;
    transition: .5s;
}

#blog .card .card-img .overlay i {
    font-size: 3rem;
    transition: .1s;
}

#blog .card .card-img .overlay a {
    transition: .1s;
}

#blog .card .card-img:hover .overlay {
    visibility: visible;
}

/* ----------Blog end---------- */



/* ----------Contact start---------- */
#contact {
    background-color: var(--third-color)
}

#contact .contact-bg {
    min-height: 100%;
    background-image: url("https://bootstrapbrain.com/demo/templates/wave/wave-lite/assets/img/contact/contact-img-1.jpg");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}


#contact .contact-content h4 {
    font-size: 2.5rem;
    font-weight: 700;
}

#contact .contact-content p {
    font-size: 1.5rem;
    font-weight: 200;
    padding-top: 1rem;
    padding-bottom: 1.5rem;
}

#contact .list-group-item i {
    font-size: 2rem;
    color: var(--btn-color);
    padding-right: 1.3rem;
}

#contact .list-group-item h5 {
    font-size: 1.6rem;
    font-weight: 600;
    padding: 0;
    margin: 0;
}

#contact .list-group-item p {
    font-size: 1rem;
    font-weight: 300;
    padding: .3rem;
    margin: 0;
}

/* ----------Contact end---------- */


/* ----------Footer start---------- */
footer {
    background-color: var(--footer-color);
    color: white;
}

footer .brand {
    font-size: 2rem;
}

.footer {
    padding: 4rem 1rem;
    font-size: 1.1rem;
    font-weight: 300;
    color: var(--secondary-text-color);
}

footer .list-group .list-group-item {
    padding: .1rem 0;
    border: none;
    background: transparent;
    color: var(--secondary-text-color);
}

footer .row h4 {
    color: var(--primary-color);
    margin-bottom: 1.5rem;
    font-weight: 600;
}

footer form .input-group i {
    font-size: 2.3rem;
    padding: .3rem;
    color: var(--primary-color);
}

footer form .input-group input {
    background: transparent;
}

footer form .input-group input::placeholder {
    color: var(--primary-color);
}

footer form .input-group input:focus {
    color: var(--primary-color);
    background: transparent;
}

footer .copyright {
    font-family: var(--primary-font);
    background-color: var(--footer-copyright-color);
    padding: 1.5rem;
    font-weight: 300;
    color: var(--secondary-text-color);
}

footer .copyright .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

footer .copyright span {
    font-family: var(--primary-font);
    font-weight: 500;
    color: var(--secondary-color);
    background: transparent;
}

footer .copyright .social-icon {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

footer .copyright .social-icon a i {
    padding: .5rem;
    font-size: 1.5rem;
    border-radius: 50%;
    background-color: var(--primary-color);
    color: var(--btn-color);
}

/* ----------Footer end---------- */





@media only screen and (max-width: 774px) {

    section {
        overflow-x: hidden;
    }

    .page-heading {
        font-size: 2.5rem;
        line-height: 3rem;
    }

    .card-container {
        gap: 1.5rem;
        grid-template-columns: auto auto;
    }

    .col-md-6 {
        width: 100% !important;
    }

    p {
        font-size: 1.5rem;
    }

    #home .outer {
        padding: 3rem 1rem;
        min-height: 100vh;
        background-image: url("https://png.pngtree.com/png-clipart/20231016/original/pngtree-businessman-pointing-at-camera-3d-render-businessman-character-illustration-png-image_13321561.png");

        background-position: top;
        background-size: cover;
        background-attachment: fixed;

    }

    #home .home-content {
        padding-top: 8rem;
    }

    #home .home-content h4 {
        font-size: 2.5rem;
    }

    #home .home-bg {
        width: 0;
        height: 0;
        visibility: hidden;
    }


    #portfolio .image-gallary {
        grid-template-columns: auto auto;
    }


    #about h4 {
        font-size: 3rem;
    }

    #team h4 {
        font-size: 3rem;
    }


    #pricing .content h4 {
        font-size: 3rem;
    }

    #blog .col-md {
        width: 100% !important;
    }

    #blog .blog-content p {
        margin: 1rem 0;
        font-size: 1.5rem;
    }

    #blog .blog-post-container {
        grid-template-columns: auto;
    }

    #contact .home-content h4 {
        font-size: 2rem;
    }

    #contact .contact-bg {
        height: 0x;
    }

    footer .copyright {
        text-align: center;
    }

    footer .social-icon {
        padding-top: 1rem;
        justify-content: space-around !important;
    }

}

@media only screen and (max-width: 478px) {
    .card-container {
        grid-template-columns: auto;
    }

    #portfolio .image-gallary {
        grid-template-columns: auto;
    }


}